import {userSendVerifycode, resetPwdStep1} from '../../api/api.js'

export default {
	data() {
		return {
			verifyCode: '',
			mobile: '',
			count: '',
			verifycodeBtnFlag: true,
			timer: null
		};
	},
	methods:{
		// 获取验证码
		getVerifycode() {
			const TIME_OUT = 60
			userSendVerifycode({
				mobile: this.mobile
			}).then(res => {
				// 提示
				uni.showToast({
					title: res.message
				})
				// 显示倒计时
				if(!this.timer) {
					this.count = TIME_OUT
					this.verifycodeBtnFlag = false
					this.timer=setInterval(() => {
						if(this.count > 0 && this.count <= TIME_OUT) {
							this.count--
						} else {
							this.verifycodeBtnFlag = true
							clearInterval(this.timer)
							this.timer = null
						}
					}, 1000)
				}
			})
		},
		// 校验验证码
		next() {
			// resetPwdStep1({
			// 	mobile: this.mobile,
			// 	verifyCode: this.verifyCode
			// }).then(res => {
			// 	uni.navigateTo({
			// 		url:"./resetPassword"
			// 	})
			// })
			let data = JSON.stringify({'key': '11ss22ss33eejjkk', 'mobile': this.mobile})
			uni.navigateTo({
				url:"./resetPassword?data=" + data
			})
		}
	}
}
